@import "../../common/style/sass/variables";
@import "../../common/style/sass/mixins";

.footer {
  position: fixed;
  padding: 0;
  bottom: 50px;
  left: 50px;
  right: 50px;
  width: auto;
  height: 1px;
  z-index: 100;

  .name {
    margin: 2px 0 0 0;
    font-size: 14px;
    line-height: 2;
    color: #bbb;
  }

  .links {
    padding-right: 75px;
    position: absolute;
    bottom: 100%;
    right: 0;
    @media screen and (max-width: 1469px) {
      right: -20px
    }
    white-space: nowrap;
    transform-origin: 100% 0;
    transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
    width: 24px;

    a {
      @include text(20px, $gray);
      margin-right: 20px;

      svg {
        transform: rotate(270deg);
        -webkit-transform: rotate(270deg);
      }
    }
  }
}
